@charset "UTF-8";
@font-face {
  font-family: "uzura";
  src: url("../fonts/uzura_font.woff2") format("woff2"), url("../fonts/uzura_font.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1280px) {
  html {
    font-size: 1.25vw;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

body {
  font-family: "dnp-shuei-gothic-gin-std", sans-serif;
  color: #555;
  font-feature-settings: "palt";
  letter-spacing: 0.1em;
  line-height: 1.5;
}

/* pcの電話番号発信対応 */
@media screen and (max-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (max-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

.u-pc {
  display: block;
}
@media screen and (max-width: 768px) {
  .u-pc {
    display: none;
  }
}

.u-sp {
  display: none;
}
@media screen and (max-width: 768px) {
  .u-sp {
    display: block;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.top-about {
  margin-top: 6.25rem;
  padding-bottom: 8.125rem;
}

.top-about__title {
  text-align: center;
}

.top-about__text {
  margin-top: 2rem;
  text-align: center;
  line-height: 2;
}

.top-about__btn {
  text-align: center;
  margin-top: 3.75rem;
}

.top-about__btn .section-btn__link {
  justify-content: center;
}

.top-fv {
  position: relative;
  padding-top: 12.5rem;
}

.top-fv::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 37.5rem;
  left: 0;
  top: 0;
  background-image: url("../images/mv-snow.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
}

.top-fv__top {
  display: flex;
  gap: min(7.5rem, 9vw);
  justify-content: center;
}

.top-fv__title-box {
  text-align: center;
  margin-left: 2.5rem;
}

.top-fv__title {
  font-size: 2.5rem;
  font-weight: 700;
}

.top-fv__sub-title {
  margin-top: 0.625rem;
  font-size: 1rem;
  font-weight: 700;
}

.top-fv__img-box {
  position: relative;
}

.top-fv__img {
  max-width: 23.625rem;
  width: 100%;
  aspect-ratio: 378/252;
  height: auto;
}

.top-fv__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-fv__img-text {
  font-size: 1rem;
  font-weight: 500;
  position: absolute;
  right: -1.25rem;
  top: 1.25rem;
}

.top-fv__img-text::after {
  content: "";
  position: absolute;
  display: block;
  width: 15.9375rem;
  height: 10.75rem;
  right: -2.8125rem;
  top: -3.625rem;
  background-image: url("../images/mv-hukidashi.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.top-fv__lists {
  margin-top: 2.625rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(14.75rem, 1fr));
  gap: 1.5625rem;
}

.top-fv__list-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 14.75rem;
  height: 14.75rem;
  border-radius: 50%;
  border: 0.3125rem solid #faf8f3;
}

.top-fv__list-img {
  max-width: 6.875rem;
  width: 100%;
  height: auto;
}

.top-fv__list-text {
  margin-top: 0.5rem;
  color: #693c19;
  font-family: "uzura", sans-serif;
}

.top-wrap,
.top-wrap__under,
.top-wrap__split {
  position: relative;
}

.top-wrap::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100vw;
  left: 0;
  top: 7.5rem;
  background-image: url("../images/about-frame3.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: drop-shadow(0 0 40px rgb(247, 222, 202));
  z-index: -1;
}

.top-wrap::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: min(19.5rem, 12vw);
  left: 0;
  bottom: 0;
  background-image: url("../images/about-frame-under.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.top-wrap__under::before {
  content: "";
  position: absolute;
  display: block;
  width: 41.6875rem;
  height: min(31.5rem, 20vw);
  left: 0;
  bottom: 0;
  background-image: url("../images/about-frame-left.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.top-wrap__under::after {
  content: "";
  position: absolute;
  display: block;
  width: 42rem;
  height: min(31.5rem, 20vw);
  right: 0;
  bottom: 0;
  background-image: url("../images/about-frame-right.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.top-wrap__split::before {
  content: "";
  position: absolute;
  display: block;
  width: 21.9375rem;
  height: 23.125rem;
  left: 0;
  bottom: 3.5rem;
  background-image: url("../images/about-tree-left.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.top-wrap__split::after {
  content: "";
  position: absolute;
  display: block;
  width: 20.875rem;
  height: 21.5625rem;
  right: 0;
  bottom: 2.75rem;
  background-image: url("../images/about-tree-right.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.top-work {
  background-color: #fcede1;
  padding-bottom: 15rem;
  position: relative;
}

.top-work__inner.inner {
  max-width: 71.25rem;
}

.top-work__title {
  text-align: center;
}

.top-work__lists {
  margin-top: 3.75rem;
}

.top-work__list {
  position: relative;
}

.top-work::before {
  content: "";
  position: absolute;
  display: block;
  width: 24.625rem;
  height: 23.625rem;
  top: 9.375rem;
  right: 0;
  background-image: url("../images/work-kamakura.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.top-work__list + .top-work__list {
  margin-top: 3rem;
}

.top-work__link {
  display: flex;
  align-items: center;
  gap: 40px;
}

.top-work__list:nth-child(even) .top-work__link {
  flex-direction: row-reverse;
}

.top-work__img {
  max-width: 26.875rem;
  width: 100%;
  height: auto;
  aspect-ratio: 430/323;
}

.top-work__img img {
  border-radius: 1.25rem;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.top-work__category {
  font-size: 0.75rem;
  font-weight: 500;
  color: #888;
  border: 0.0625rem solid #888;
  padding: 0.25rem 0.5rem;
  border-radius: 0.625rem;
  width: fit-content;
}

.top-work__head {
  font-size: 1.25rem;
  font-weight: 700;
  margin-top: 0.25rem;
}

.top-work__text {
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
}

.top-work__btn-link {
  margin-top: 2rem;
}

.top-work__btn {
  margin-top: 3.75rem;
}

.top-work__btn .section-btn__link {
  justify-content: center;
}

.header {
  position: fixed;
  z-index: 900;
  top: 0;
  right: 0;
  left: 0;
}

.header::before {
  content: "";
  position: absolute;
  display: block;
  width: 20.75rem;
  height: 15.125rem;
  left: -4.375rem;
  top: -4.875rem;
  background-image: url("../images/common/header-moku.webp");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  z-index: -1;
  filter: drop-shadow(0 0 40px rgb(247, 222, 202));
}

.header__inner {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin: 2.5rem 0 0 2.5rem;
  max-width: 7.25rem;
  width: 100%;
  height: inherit;
}
@media screen and (max-width: 768px) {
  .header__logo {
    max-width: 5rem;
  }
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.header__logo img {
  height: 100%;
  object-fit: contain;
}

.header__nav {
  margin: 2.5rem 5rem 0 0;
  padding: 1rem 1.25rem;
  border-radius: 62.4375rem;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 140px rgb(247, 222, 202);
  backdrop-filter: blur(22px);
}
@media screen and (max-width: 768px) {
  .header__nav {
    display: none;
  }
}

.header__nav-list {
  display: flex;
}

.header__nav-item {
  height: 100%;
}

.header__nav-item a {
  padding-inline: 1.25rem;
  height: 100%;
  display: flex;
  align-items: center;
  font-size: 1rem;
  font-weight: 500;
  color: #555;
  position: relative;
}

.header__hamburger {
  display: none;
}
@media screen and (max-width: 768px) {
  .header__hamburger {
    position: relative;
    z-index: 999;
    width: 2.5rem;
    height: inherit;
    cursor: pointer;
    transition: 0.3s;
    display: block;
  }
}

.header__hamburger.is-open {
  background-color: transparent;
}

.header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 1.5rem;
  height: 0.25rem;
  border-radius: 0.3125rem;
  background-color: #646464;
  transition: 0.5s;
}

.header__hamburger span:nth-of-type(1) {
  top: -0.375rem;
}

.header__hamburger span:nth-of-type(2) {
  top: 0;
}

.header__hamburger span:nth-of-type(3) {
  top: 0.375rem;
}

.header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.header__hamburger.is-open span:nth-of-type(3) {
  top: -0.5rem;
  transform: translateX(-50%) rotate(-45deg);
}

.header__drawer {
  padding: 7.5rem 0;
  position: absolute;
  z-index: 900;
  top: 0;
  right: -120%;
  width: 100%;
  height: 100vh;
  background-color: #ece5de;
  overflow-y: scroll;
  scrollbar-width: none;
  transition: 0.6s;
}

.header__drawer.is-open {
  right: 0;
}

.header__drawer::-webkit-scrollbar {
  display: none;
}

.header__drawer-item a {
  padding: 1.25rem 0;
  display: block;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
  color: #555;
  text-align: center;
}

.inner {
  max-width: 1330px;
  padding-right: 25px;
  padding-left: 25px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 768px) {
  .inner {
    padding-right: 20px;
    padding-left: 20px;
  }
}

.section-btn__link {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.section-btn__link-text {
  line-height: 2;
  color: #555;
  font-size: 1rem;
  font-weight: 500;
}

.section-btn__link-arrow {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #faf8f3;
  border: 0.0625rem solid #fcede1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-btn__link-arrow img {
  width: 1rem;
  height: 1rem;
}

.section-title__en {
  color: #a77d5c;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 0.75rem;
}

.section-title__jp {
  font-size: 2rem;
  font-weight: 700;
}